@import "./Main.css";

.home
{
    width:100vw;
    justify-content: center;
}
.block
{
    /* @extend .page; */
    margin-bottom: 5vh;
   background-color: #acff0738;
   width: 80%;
   min-width: 300px;
   justify-self: center;
   margin-left: auto;
   margin-right: auto;
   border-radius: 30px;
   border-color:  rgba(172, 255, 7, 0.219);
   border-width: 10px;
   border-style: none;

   -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.75);

    max-width: 1000px;


}

.block .header
{
    padding-top: 10px;
    border-color: magenta;
    background-color: rgba(172, 255, 7, 0.239);
    width: 100%;
    border-style: none none solid none;
    font-size: x-large;
    display: flex;
    flex-wrap: wrap;
    border-radius: 30px 30px 0px 0px;
    border: none;
    justify-content: center;

}


.topic
{
    margin-left: 1vw;
    background-color: transparent;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    vertical-align: middle;
    min-width: 120px;
    margin-bottom: 1vh;
    padding: 10px;



}
.header .title
{
    width: 50%;
    padding-left: 10%;
    padding-top: 1%;
    min-width: 200px;
}

.topics
{
    margin-top: 10px;
    margin-bottom: 10px;
   width: 100%;
   display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-left: auto;
    margin-right: auto;
}

.block.small .topic
{
    margin-top: 10px;
    width: 42%;
    min-width: 150px;
    border-radius: 10px;
    border-style: dashed;
    border-style: none;;
    margin-left: auto;
    margin-right: auto;

    -webkit-box-shadow: 0px 0px 10px 10px rgba(0,0,0,0.16);
    -moz-box-shadow: 0px 0px 10px 10px rgba(0,0,0,0.16);
    box-shadow: 0px 0px 10px 10px rgba(0,0,0,0.16);
}

.block.small .topic:hover
{
    background-color: #acff0738;
    transform: scale(1.1);
}

.block.small
{
    display: grid;
    grid-template: auto / auto;
    /* grid-template: auto / 1fr 1fr 1fr 1fr 1fr; */
}

.header .art
{
    width: 15%;
    min-width: 110px;
    height: auto;

}
/*
.topic.small
{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
} */

.topic .text
{
    width: 85%;
    font-size: medium;
}

.topic .art
{
    width: 15%;
    min-width: 80px;
    height: auto;
}
.topic img
{
    width: 100%;
    height: auto;
}