@import './Main.css';


.services
{
    padding-top:2vh;
    justify-content: center;
  display: flex;
  flex-wrap: wrap;
  width: 100vw;
  background-color: goldenrod;
  height: 100vh;
  transition: 1s;
}

.services .bubble
{

  background-color: #bbb;
  border-radius: 5vh;
  transition: 0.5s;
  margin: 5%;
  height: 25vh;
  width: 40%;
  min-width: 300px;
  text-align: center
}
.services .bubble:hover
{
  background-color: #aab;
  transition: 0.5s;
  width: 48%;
  margin: 1%;
  margin-top: 5%;
  margin-bottom: 5%;
}

.services .bubble p
{

  margin-top: 2%;
  vertical-align: middle;
  text-align: center;

}
.services li {
    margin-top:1vh;
    padding-top:10px;
    margin-left: 20px;
    vertical-align: middle;
    text-align: left;
}